.block-testimonials {
    .testimonials {
        @include make-row();
        --bs-gutter-y: #{$grid-gutter-width};
        @include scroll-snap;
        > * {
            @include make-col-ready();
            @include media-breakpoint-up(md) {
                @include make-col(4); } } }

    &-carousel,
    &-grid {
        .quote {
            --bs-card-bg: #{$light};
            --bs-card-border-width: 0;
            --bs-card-cap-bg: transparent;
            --bs-card-cap-padding-y: 1rem;
            @extend .card;
            height: 100%;
            @include media-breakpoint-up(md) {
                --bs-card-spacer-x: 1.5rem;
                --bs-card-spacer-y: 1.5rem;
                --bs-card-cap-padding-x: 1.5rem;
                --bs-card-cap-padding-y: 1.5rem; }
            @include media-breakpoint-up(lg) {
                --bs-card-spacer-x: 2rem;
                --bs-card-spacer-y: 2rem;
                --bs-card-cap-padding-x: 2rem;
                --bs-card-cap-padding-y: 2rem; }
            blockquote {
                @extend .card-body;
                p {
                    font-size: $small-font-size;
                    line-height: $line-height-base;
                    @include media-breakpoint-up(md) {
                        font-size: $font-size-base; } } }
            blockquote + figcaption {
                @extend .card-footer; } } }

    .splide {
        &__track {
            overflow: hidden; }
        &__list {
            display: flex; }
        &__slide {
            flex-shrink: 0;
            opacity: .5;
            transition: opacity .3s ease;
            width: calc(100% / 3 + #{$grid-gutter-width});
            &.is-next,
            &.is-prev {
                opacity: .75; }
            &.is-active {
                opacity: 1; } }
        &__pagination {
            bottom: -2rem; } }

    &-list {
        @include media-breakpoint-up(md) {
            .container {
                display: grid;
                gap: $grid-gutter-width;
                grid-template-columns: 1fr 1fr; }
            .heading {
                align-items: flex-start;
                margin-bottom: 0;
                margin-left: 0;
                max-width: none;
                text-align: left; }
            .items {
                margin-top: 3rem; }
            .footing {
                grid-column: 1 / 3; } }
        .items {
            list-style-type: none;
            margin-bottom: 0;
            padding-left: 0;
            li {
                margin-bottom: .5rem;
                margin-top: .5rem; } }
        .quote {
            blockquote {
                text-align: left; }
            blockquote + figcaption {
                justify-content: flex-start;
                text-align: left; }
            figcaption {
                margin-top: .5rem; }
            figcaption br {
                display: none; } } } }
