.heading {
    @include margin-bottom($global-spacing-semi);
    &,
    & > .container {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        gap: .25em; }

    h1, h2, p {
        margin-bottom: 0;
        text-wrap: balance; }
    .text,
    .cta {
        @include margin-top($headings-margin-bottom); }

    .metas {
        .btn {
            --bs-btn-font-size: #{$small-font-size};
            --bs-btn-padding-x: .5rem;
            --bs-btn-padding-y: .5rem; } }

    &-filter {
        align-items: baseline;
        flex-direction: row;
        justify-content: space-between;
        .surtitle {
            flex-basis: 100%; } }

    @include media-breakpoint-up(md) {
        h1 {
            max-width: 80%; }
        h1 + .metas {
            margin-top: .75rem; }
        .more {
            @include more; } }

    @include media-breakpoint-up(xl) {
        h1 {
            max-width: 66.6666666667%; } } }
