.block-cta {
    .btn {
        text-wrap: pretty; }
    .btn:first-of-type {
        @include button-variant($primary,$primary); }
    .btn + .btn {
        @include button-outline-variant($primary); }

    @include media-breakpoint-down(md) {
        .btn {
            display: block; } }

    @include media-breakpoint-up(lg) {
        text-align: center;
        .cta {
            align-items: center;
            justify-content: center; } } }
