/*! purgecss start ignore */
[class*="block-"] {
    @include padding-bottom($global-spacing);
    @include padding-top($global-spacing);
    .heading {
        align-items: flex-start;
        flex-direction: column; }
    .footing {
        @include margin-top($global-spacing); }

    @include media-breakpoint-up(lg) {
        .heading,
        .footing {
            align-items: center;
            margin-left: auto;
            margin-right: auto;
            max-width: 66%;
            text-align: center;
            > .container {
                align-items: center; } }
        .heading {
            margin-bottom: $global-spacing; }
        [class*="col-"] {
            .heading,
            .footing {
                max-width: 100%; } } } }
/*! purgecss end ignore */
