.block-editorial {
    .row {
        align-items: center;
        .content {
            @extend .col-md-6; }
        .media {
            @extend .col-md-6; } }
    h2, p {
        text-wrap: balance; }
    .text:not(:last-child) {
        margin-bottom: $paragraph-margin-bottom; }
    p:last-child {
        margin-bottom: 0; }
    .btn {
        @include button-variant($primary,$primary); }
    figure {
        display: block;
        margin-bottom: 0; }
    img {
        height: auto;
        width: 100%; }
    .screenshot {
        margin-bottom: 0;
        margin-top: 0; }
    &-rtl {
        .row {
            flex-direction: row-reverse; } }

    @include media-breakpoint-down(md) {
        .media {
            --bs-gutter-x: calc(#{$gutter-mobile} - (#{$grid-gutter-width} * .5) );
            margin-bottom: $spacer;
            margin-left: calc(-1 * var(--bs-gutter-x));
            margin-right: calc(-1 * var(--bs-gutter-x));
            max-width: none;
            padding-left: 0;
            padding-right: 0;
            order: -1;
            width: calc(2 * var(--bs-gutter-x) + 100%); } } }
